body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Nunito', Arial, sans-serif; }

h1, h2, h3, h4 {
  padding: 0;
  margin: 0; }

a {
  text-decoration: none; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0; }

.page-header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.15); }

.header-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 19px 0 19px; }

.header-nav--left,
.header-nav--right {
  display: flex; }

.header-nav--left .header-nav__list {
  width: 173px;
  justify-content: space-around; }

.header-nav--right .header-nav__list {
  margin: 0 30px; }

.header-nav--right .header-nav__list:nth-last-child() {
  margin: 0; }

.header-nav__logo {
  padding-right: 30px; }

.header-nav__list {
  display: flex;
  align-items: center; }

.header-nav__link {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #000000; }

.header-nav__link:hover {
  opacity: 0.5; }

.header-nav__link:active {
  opacity: 0.3; }

.header-nav--right .header-nav__item {
  margin-right: 30px; }

.header-nav--right .header-nav__item:last-child {
  margin: 0; }

.header-nav__button {
  margin-left: 10px;
  padding: 12px 32px;
  border: 1px solid #000000;
  border-radius: 48px;
  color: #222222; }

.header-nav__button:hover {
  opacity: 0.5; }

.header-nav__button:active {
  opacity: 0.3; }

.promo {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 74px; }

.promo__item {
  width: 480px;
  display: flex;
  flex-direction: column; }

.promo__title {
  margin: 0;
  font-size: 42px;
  line-height: 50px;
  font-weight: 400;
  color: #222222;
  padding-top: 31px;
  padding-bottom: 23px; }

.promo__description {
  margin: 0;
  font-size: 18px;
  line-height: 26px;
  color: #222222;
  padding-bottom: 13px; }

.promo__item small {
  position: relative;
  order: -1;
  padding-left: 37px; }

.promo__item small::before {
  position: absolute;
  content: '';
  background: url(../img/ico-play1.svg);
  width: 29px;
  height: 29px;
  top: -8px;
  left: 0; }

.promo__app-link {
  margin-bottom: 20px; }

.promo__image img {
  padding-top: 40px; }

.promo__app-link,
.promo__google-link {
  width: 170px; }

.promo__app-link:hover,
.promo__google-link:hover {
  opacity: 0.8; }

.promo__app-link:active,
.promo__google-link:active {
  opacity: 0.7; }

.features {
  padding-bottom: 46px; }

.features__guarantee,
.features__care {
  display: flex;
  align-items: flex-end;
  justify-content: space-between; }

.features__guarantee-item {
  padding-bottom: 99px; }

.features__guarantee-item,
.features__care-item {
  width: 480px; }

.features__guarantee-title,
.features__care-title {
  font-size: 36px;
  line-height: 43px;
  color: #222222;
  font-weight: 400; }

.features__guarantee-description,
.features__care-description {
  font-size: 18px;
  line-height: 26px;
  color: #222222; }

.features__cashback {
  width: 600px;
  margin: 0 auto; }

.features__cashback-title {
  padding-top: 208px;
  text-align: center;
  font-size: 36px;
  line-height: 43px;
  font-weight: 400;
  -moz-columns: #222222;
       columns: #222222; }

.features__cashback-description {
  padding: 0 8px;
  margin: 0;
  text-align: center;
  font-size: 18px;
  line-height: 26px; }

.partners {
  padding-bottom: 47px; }

.partners__list {
  display: grid;
  grid-template-columns: repeat(4, 300px); }

.partners__button {
  display: block;
  margin: auto;
  text-align: center;
  width: 237px;
  color: #222222;
  padding: 12px 32px;
  border-radius: 48px;
  border: 1px solid #000000;
  margin-bottom: 30px; }

.partners__button:hover {
  opacity: 0.5; }

.partners__button:active {
  opacity: 0.3; }

.parnters__list-item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 0; }

.payment {
  padding-bottom: 125px; }

.payment__mastercard,
.payment__online,
.payment__account,
.payment__quick {
  display: flex;
  align-items: flex-end;
  justify-content: space-between; }

.payment__mastercard {
  padding-bottom: 133px; }

.payment__online {
  padding-bottom: 116px; }

.payment__account {
  padding-bottom: 149px; }

.payment__mastercard-item,
.payment__online-item,
.payment__account-item,
.payment__quick-item {
  width: 480px; }

.payment__mastercard-item {
  margin-bottom: 58px; }

.payment__mastercard-title,
.payment__online-title,
.payment__account-title,
.payment__quick-title {
  font-size: 36px;
  line-height: 43px;
  font-weight: 400;
  color: #222222; }

.payment__mastercard-description,
.payment__online-description,
.payment__account-description,
.payment__quick-description {
  font-size: 18px;
  line-height: 26px;
  color: #222222; }

.payment__mastercard-description {
  padding-bottom: 28px; }

.payment__appstore-link,
.payment__googleplay-link {
  margin-right: 20px; }

.payment__appstore-link:hover,
.payment__googleplay-link:hover {
  opacity: 0.8; }

.payment__appstore-link:active,
.payment__googleplay-link:active {
  opacity: 0.6; }

.payment__applepay-link,
.payment__googlepay-link {
  margin-right: 20px; }

.payment__applepay-link:hover,
.payment__googlepay-link:hover {
  opacity: 0.8; }

.payment__applepay-link:active,
.payment__googlepay-link:active {
  opacity: 0.7; }

.footer-nav {
  display: flex;
  justify-content: space-between;
  padding-top: 69px;
  border-top: 1px solid #CBD6E2; }

.footer-nav__title {
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #222222;
  margin-bottom: 33px; }

.footer-nav__list-item {
  padding-bottom: 15px; }

.footer-nav__list-link {
  font-size: 14px;
  line-height: 19px;
  font-weight: 400;
  color: #000000; }

.footer-nav__list-link:hover {
  opacity: 0.7; }

.footer-nav__list-link:active {
  opacity: 0.5; }

.license {
  position: relative;
  padding-top: 55px;
  padding-bottom: 65px; }

.license-description {
  margin-top: 26px;
  width: 836px;
  font-size: 12px;
  line-height: 16px;
  color: #aaaaaa; }

.license::after {
  position: absolute;
  content: '';
  background: url(../img/pci.png);
  width: 176px;
  height: 60px;
  right: 0;
  bottom: 65px; }
